import React from 'react';
import { GoMarkGithub } from "react-icons/go";
import '../App.css';
import LightBox from './LightBox';

class Card extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      displayLightBox: false,
    }
  }

  render() {
    const { displayLightBox } = this.state;
    const { title, link, github, picture, desc, carousel } = this.props;

    return (
      <a className="card" href={ link } target="_blank" onClick={ carousel ? () => { this.setState({ displayLightBox: true}) } : null }>
        { displayLightBox ? <LightBox display={ displayLightBox } images={ carousel } /> : null }
        <img className="card-pic" src={ process.env.PUBLIC_URL + '/projects-thumbs/' + picture } alt="Screenshot" />
        <div className="card-body">
          <div className="row">
            <h3 className="card-title">{ title }</h3>
            {github
              ? <a href={ github } target="_blank"><GoMarkGithub className="github-logo" style={{ color: 'black', marginTop: 7 }} /></a>
              : null
            }
          </div>
          <div className="card-desc">{ desc }</div>
        </div>
      </a>
    )
  }
}

export default Card
